.geo-information {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
  //background: linear-gradient(#fff, #ffa);
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  //box-shadow: 0px 2px 2px rgb(5, 5, 5);
  color: rgb(209, 174, 107);
  opacity: 70%;
  font-family: "Open Sans", sans-serif;
  font-size: xx-large;
  height: 100px;
  margin: 0px;
  text-align: center;
  text-shadow: 0 2px 2px #666;
  width: 100%;

  .location {
    display: flex;
    flex-direction: row;
    justify-content: center;
    height: 60px;
    width: 50%;

    .loc-info {
      display: flex;
      text-align: center;
      height: 50px;
      width: 25%;
      margin: 1px 1px 6px;
    }
  }
}
