.loadpage{width:100%;height:600px;display:flex;flex-direction:column;justify-content:center;align-items:center}.loadpage .title{width:100%;text-align:center;font-size:3rem;margin:0px}

.geo-information{display:flex;flex-direction:row;justify-content:space-between;align-items:flex-start;border-bottom-left-radius:5px;border-bottom-right-radius:5px;color:#d1ae6b;opacity:70%;font-family:"Open Sans", sans-serif;font-size:xx-large;height:100px;margin:0px;text-align:center;text-shadow:0 2px 2px #666;width:100%}.geo-information .location{display:flex;flex-direction:row;justify-content:center;height:60px;width:50%}.geo-information .location .loc-info{display:flex;text-align:center;height:50px;width:25%;margin:1px 1px 6px}

.weather{display:flex;flex-direction:row;justify-content:center;align-items:flex-start;border-bottom-left-radius:5px;border-bottom-right-radius:5px;color:black;opacity:100%;font-family:"Open Sans", sans-serif;font-size:x-large;height:60px;margin:0px;text-align:center;text-shadow:0 2px 2px #666;width:50%}.weather .wth-info{display:flex;align-items:center;font-family:"Open Sana", sans-serif;text-align:center;height:50px;width:25%;margin:1px 1px 6px}

.time{display:flex;height:45px;justify-content:center;box-shadow:0px 2px 2px #050505;width:100%;opacity:80%;border-top-left-radius:2px;border-top-right-radius:2px}.time .unit{background:-webkit-gradient(linear, left top, left bottom, from(#3a8be9), to(#3bcfe9));background:linear-gradient(#3a8be9, #3bcfe9);border-radius:5px;box-shadow:2px 2px 2px #3a8be9;color:#fff;font-family:"Open Sans", sans-serif;font-size:xx-large;height:100%;margin:0 5px;text-align:center;text-shadow:0 2px 2px #666;width:4%}

.sign-in{width:380px;display:flex;flex-direction:column}.sign-in .title{margin:10px 0}.sign-in .buttons{display:flex;justify-content:space-between}

.group{position:relative;margin:45px 0}.group .form-input{background:none;background-color:white;color:grey;font-size:18px;padding:10px 10px 10px 5px;display:block;width:100%;border:none;border-radius:0;border-bottom:1px solid grey;margin:25px 0}.group .form-input:focus{outline:none}.group .form-input:focus ~ .form-input-label{top:-14px;font-size:12px;color:#000}.group input[type="password"]{letter-spacing:0.3em}.group .form-input-label{color:grey;font-size:16px;font-weight:normal;position:absolute;pointer-events:none;left:5px;top:10px;-webkit-transition:300ms ease all;transition:300ms ease all}.group .form-input-label.shrink{top:-14px;font-size:12px;color:#000}

.custom-button{min-width:165px;width:auto;height:50px;letter-spacing:0.5px;line-height:50px;padding:0 35px 0 35px;font-size:15px;background-color:black;color:white;text-transform:uppercase;font-family:"Open Sans Condensed";font-weight:bolder;border:none;cursor:pointer}.custom-button:hover{background-color:white;color:black;border:1px solid black}.custom-button.google-sign-in{background-color:#4285f4;color:white}.custom-button.google-sign-in.hover{background-color:#357ae8;border:none}

* {
  box-sizing: border-box;
}

