.weather {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
  //background: linear-gradient(#fff, #ffa);
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  //box-shadow: 0px 2px 2px rgb(5, 5, 5);
  color: black;
  opacity: 100%;
  font-family: "Open Sans", sans-serif;
  font-size: x-large;
  height: 60px;
  margin: 0px;
  text-align: center;
  text-shadow: 0 2px 2px #666;
  width: 50%;

  .wth-info {
    display: flex;
    align-items: center;
    font-family: "Open Sana", sans-serif;
    text-align: center;
    height: 50px;
    width: 25%;
    margin: 1px 1px 6px;
  }
}
